@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.container{
  background: #d7d9d8;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.upperSection{
  height: 13rem;
  background-color: #0ed745;
  padding-top: 3.5rem;
}

.upperSection span{
  font-size: 32px;
  font-weight: 700;
  color: aliceblue;
  margin-left: 4rem;
}

.lowerSection{
  /* background-color: #8d908e; */
  margin: 0 8rem 0 8rem;
}
.ant-steps-item-process > .ant-steps-item-container > .ant-steps-item-icon {
    background: #0ed745 !important;
}

.ant-steps-item-process .ant-steps-item-icon {
    background-color: #fff;
    border-color: #0ed745 !important;
}

.ant-steps-item-process .ant-steps-item-icon:focus-visible {
    background-color: #fff;
    border-color: #0ed745 !important;
}

/* After visiting the step */
.ant-steps-item-finish .ant-steps-item-icon {
    background-color: #fff;
    border-color: #0ed745;
}

/* Line in between steps */
.ant-steps-item-title::after {
    height: 2px !important;
    background: #0ed745;
}

.ant-steps-item-finish > .ant-steps-item-container > .ant-steps-item-content > .ant-steps-item-title::after {
    background-color: #0ed745 !important;
}

/* Steps without any action */
.ant-steps-item-wait .ant-steps-item-icon {
    background-color: #fff;
    border-color: #a7a7a8 !important;
}

/* Steps after clicked */
.ant-steps-item-finish .ant-steps-item-icon {
    background-color: #0ed745;
    border-color: #0ed745;
}

.anticon svg{
    color: white;
}


/* Table css */

.dataTable table tr:nth-child(odd){
    background-color: #f8f9fa !important;
}

.dataTable table tr:hover{
    background-color: #f8f9fa !important;
}

.dataTable{
    border-radius: 20px;
}
.userName{
    font-size: large;
    color: #252525;
    font-weight: 600;
}

.userRole{
    font-size: small;
    color: #858586;
    font-weight: 400;
}

/* Rewards css */

.doller{
    font-size: x-large;
    color: #252525;
    font-weight: 700;
}

.amount{
    font-size: large;
    color: #858586;
    font-weight: 500;
}
